.theme-dark {
  --left-color: #121212;
  --right-color: #6c34a3;
  --svg-color: white;
}
.theme-light {
  --left-color: #4831d4;
  --right-color: #ccf381;
  --svg-color: black;
}
svg {
  color: var(--svg-color);
}
.opacity1 {
  opacity: 1;
}
.opacity0 {
  opacity: 0;
}
.App {
  position: relative;
}
@keyframes orbit {
  100% {
    transform: rotate(1turn);
  }
}
@keyframes hold-position {
  100% {
    transform: rotate(-1turn);
  }
}
@keyframes EnlargeAnimation {
  0% {
    width: 0;
    height: 0;
  }
  100% {
    width: 100%;
  }
}

section {
  min-height: 200px;
  color: black;
}
section > h1 {
  text-align: center;
  text-decoration: underline;
  /* text-decoration-color: var(--color-2); */
  /* color: var(--color-3); */
  width: 100%;
  margin: 0;
  padding: 0.67em 0;
  font-size: 4rem;
}
section > p {
  font-size: 1.25rem;
  color: var(--font-color);
}

section > ul {
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
#about_me {
  background-color: var(--section-background);
}
#projects {
  background-color: var(--section-background);
}
